  display: inline-block;
  vertical-align: top;
  margin-right: 50px;
}

/* Footer left */

.footer-distributed .footer-left {
  width: 40%;
  margin-left: 120px;
  margin-right: 80px;
  padding-right: 10px;
}

/* The company logo */

.footer-distributed h3 {
  color: #ffffff;
  font: normal 36px 'Open Sans', cursive;
  margin: 0;
}

.footer-distributed h3 span {
  color: lightseagreen;
}

/* Footer links */

.footer-distributed .footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 20px;
}

.footer-distributed .footer-links a {
  display: inline-block;
  line-height: 1.8;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
}

.footer-distributed .footer-company-name {
  color: #222;
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

/* Footer Center */
.icon-info2 {
  position: horizontal;
  padding-right: 30px;
  margin-right: 20px;
}

