  display: flex;
  align-items: center;
}

.footer-distributed .footer-center .icon-info p {
  margin-left: 10px;
}

.footer-distributed .footer-center {
  width: 35%;
  display: flex;
  justify-content: center;
}

.footer-distributed .footer-center div {
  display: flex;
  align-items: center;
}

.footer-distributed .footer-center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 70px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  line-height: 50px;
